<a name="process295"></a>

<p><br></p>
<p><span class="rvts17">劝说的工作原理及其算法：</span></p>
<p><span class="rvts21">在《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》中，劝说（</span><span class="rvts20">Persuasion</span><span class="rvts21">）是一项很重要的技能，并且在某些场合和任务中，甚至会影响到整个故事的结局。</span>
<br><span class="rvts21">一般情况下，较高的劝说值会带来更加理想的</span><span class="rvts20"></span><span class="rvts21">说服</span><span class="rvts20"></span><span class="rvts21">效果。然而在一小部分条件下，劝说根据语境的不同或语言内容的不同，会出现某些特定的成功效果或失败效果。接下来，笔者就将《原罪</span><span class="rvts20">2</span><span class="rvts21">》中的劝说原理讲给大家。</span>
<br><span class="rvts48"></span>
<br><span class="rvts33">1. </span><span class="rvts29">劝说的难度等级</span>
<br><span class="rvts21">劝说的难度等级分为</span><span class="rvts20">5</span><span class="rvts21">种。他们有难到易的顺序为：失败（不可能成功）</span><span class="rvts20"> -&gt; </span><span class="rvts21">困难（难以成功）</span><span class="rvts20"> -&gt;</span><span class="rvts21">正常（</span><span class="rvts20"> </span><span class="rvts21">可以成功）</span><span class="rvts20"> -&gt; </span><span class="rvts21">简单（容易成功）</span><span class="rvts20"> -&gt; </span><span class="rvts21">成功（总能成功）。</span>
<br><span class="rvts21">我们把劝说难度列表具体如下：</span></p>
<div><table width="753" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="278" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">劝说难度</span></p>
</td>
<td width="448" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">难度系数（</span><span class="rvts27">b</span><span class="rvts26">）</span></p>
</td>
</tr>
<tr valign="top">
<td width="278" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">简单</span></p>
</td>
<td width="448" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">-1</span></p>
</td>
</tr>
<tr valign="top">
<td width="278" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">正常</span></p>
</td>
<td width="448" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts39">0</span></p>
</td>
</tr>
<tr valign="top">
<td width="278" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">困难</span></p>
</td>
<td width="448" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">1</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts48"></span>
<br><span class="rvts33">2. </span><span class="rvts29">劝说的对象对劝说等级的需求</span>
<br><span class="rvts21">对</span><span class="rvts20">NPC</span><span class="rvts21">角色来说，随着</span><span class="rvts20">NPC</span><span class="rvts21">角色等级越高，则需求更高的劝说等级，一般情况下，他们每隔</span><span class="rvts20">5</span><span class="rvts21">级会增加</span><span class="rvts20">1</span><span class="rvts21">点额外的劝说难度。</span>
<br><span class="rvts21">具体需求列表如下：</span></p>
<div><table width="753" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="248" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts27">NPC</span><span class="rvts26">等级</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">劝说等级需求（</span><span class="rvts27">a</span><span class="rvts26">）</span></p>
</td>
</tr>
<tr valign="top">
<td width="248" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">1-5</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">1</span></p>
</td>
</tr>
<tr valign="top">
<td width="248" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">6-10</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">2</span></p>
</td>
</tr>
<tr valign="top">
<td width="248" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">11-15</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">3</span></p>
</td>
</tr>
<tr valign="top">
<td width="248" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">16-20</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">4</span></p>
</td>
</tr>
<tr valign="top">
<td width="248" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">21-25</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">5</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts48"></span>
<br><span class="rvts33">3. </span><span class="rvts29">劝说是否成功的计算公式</span>
<br><span class="rvts21">假如我们当前某角色的劝说等级为</span><span class="rvts20">c</span><span class="rvts21">，那么根据上面两个劝说条件，可以按照以下公式得出结论：</span>
<br><span class="rvts21">当</span><span class="rvts20">c&lt;a+b</span><span class="rvts21">，结果将会</span><span class="rvts20"></span><span class="rvts21">失败（不可能成功）</span><span class="rvts20"></span><span class="rvts21">；</span>
<br><span class="rvts21">当</span><span class="rvts20">c=a+b</span><span class="rvts21">，结果将会成功，前提是你的态度至少高于该</span><span class="rvts20">NPC</span><span class="rvts21">两点，否则依然也会失败；</span>
<br><span class="rvts21">当</span><span class="rvts20">c&gt;a+b</span><span class="rvts21">，结果将会成功通过。</span>
<br><span class="rvts48"></span>
